.sidebarComp .sidebarFields {
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.sidebarComp .sidebarFields .tabOptions {
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.sidebarComp .sidebarFields .tabOptions .mainBtnsGroup {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
}
.sidebarComp .sidebarFields .tabOptions .mainBtnsGroup .deleteBtn {
  background-color: #d22d3d;
  border: none;
  border-radius: 3px;
  color: white;
  padding: 0 10px;
}
.sidebarComp .sidebarFields .btnsGroup {
  display: flex;
  gap: 4px;
}
.sidebarComp .sidebarFields .btnsGroup button {
  border-radius: 3px;
  border: none;
  padding: 4px 12px;
  background: rgba(7, 99, 177, 0.1);
  color: var(--accentBlueColor);
}
.sidebarComp .sidebarFields .btnsGroup button.active {
  background-color: var(--accentBlueColor);
  color: white;
}
.sidebarComp .sidebarFields .sidebarField {
  display: flex;
  gap: 10px;
  align-items: center;
}
.sidebarComp .sidebarFields .sidebarField .label {
  flex: 0 0 80px;
  margin-bottom: 0;
  color: #666;
}
.sidebarComp .sidebarFields .sidebarField .fieldWrapper {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 10px;
}
.sidebarComp .sidebarFields .sidebarField .fieldWrapper .numberFieldGroup {
  display: flex;
  align-items: stretch;
  width: 120px;
  height: 28px;
}
.sidebarComp .sidebarFields .sidebarField .fieldWrapper .numberFieldGroup .button {
  border: 1px solid #CCC;
  border-radius: 3px;
  flex: 0 0 30px;
  background-color: #EEE;
  font-size: 18px;
  line-height: 8px;
  font-weight: 400;
  cursor: pointer;
}
.sidebarComp .sidebarFields .sidebarField .fieldWrapper .numberFieldGroup .button.decrement {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-right: 0;
}
.sidebarComp .sidebarFields .sidebarField .fieldWrapper .numberFieldGroup .button.increment {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-left: 0;
}
.sidebarComp .sidebarFields .sidebarField .fieldWrapper .numberFieldGroup .button:hover {
  background-color: #DDD;
}
.sidebarComp .sidebarFields .sidebarField .fieldWrapper .numberFieldGroup .input {
  border: 1px solid #CCC;
  flex: 1;
  width: 0;
  padding-left: 8px;
  outline: none;
}
.sidebarComp .sidebarFields .sidebarField .fieldWrapper .colorFieldGroup {
  display: flex;
  align-items: stretch;
  width: 120px;
  height: 28px;
}
.sidebarComp .sidebarFields .sidebarField .fieldWrapper .colorFieldGroup .colorField {
  border: 1px solid #CCC;
  padding: 0;
  border-radius: 3px 0 0 3px;
  width: 32px;
  height: 28px;
}
.sidebarComp .sidebarFields .sidebarField .fieldWrapper .colorFieldGroup .inputField {
  border: 1px solid #CCC;
  border-left: 0;
  flex: 1;
  width: 0;
  border-radius: 0 3px 3px 0;
  text-transform: uppercase;
  padding: 0 4px;
  outline: none;
}
.sidebarComp .sidebarFields .sidebarField .fieldWrapper .rotateButton {
  border: 1px solid #CCC;
  height: 28px;
  width: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 3px;
  cursor: pointer;
}
.sidebarComp .sidebarFields .sidebarField .fieldWrapper .rotateButton svg {
  flex: 0 0 16px;
}
.sidebarComp .selectField {
  opacity: 1;
}
.sidebarComp .selectField :global .custom__control {
  border-color: hsl(0, 0%, 80%);
  box-shadow: none;
  min-height: auto;
  cursor: pointer;
}
.sidebarComp .selectField :global .custom__control .custom__indicators .custom__indicator {
  padding: 4px;
}
.sidebarComp .selectField :global .custom__menu {
  margin-top: 0;
}
.sidebarComp .selectField :global .custom__menu .custom__menu-list .custom__option {
  padding: 5px 12px;
  cursor: pointer;
}
.sidebarComp .textareaField {
  flex: 1;
  padding: 2px 6px;
  border: 1px solid #ccc;
  border-radius: 3px;
  outline: none;
}
.sidebarComp .textField {
  flex: 1;
  padding: 2px 6px;
  border: 1px solid #ccc;
  border-radius: 3px;
  outline: none;
}