#threejs-map-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  flex: 1;
  user-select: none;
}

#threejs-map {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.map_controls {
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 1;
}
.map_controls :global .map_controls_wrapper {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.map_controls :global .map_controls_wrapper .control_btn {
  color: #FFF;
  background-color: #0763b1;
  font-size: 14px;
  font-weight: 500;
  padding: 6px 15px;
  border: none;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 300ms;
  width: 100%;
  flex: 1;
  text-align: center;
  letter-spacing: 0.03rem;
  height: 32px;
}
.map_controls :global .map_controls_wrapper .control_btn:hover {
  background-color: #054983;
}
.map_controls :global .map_controls_wrapper .control_btn.active {
  background-color: #054983;
  cursor: default;
}
.map_controls :global .map_controls_wrapper .floors {
  align-items: normal;
}
.map_controls :global .map_controls_wrapper .floors .floor:not(:first-child):not(:last-child) {
  border-radius: 0;
}
.map_controls :global .map_controls_wrapper .floors .floor:first-child {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
.map_controls :global .map_controls_wrapper .floors .floor:last-child {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
.map_controls :global .map_controls_wrapper .reset-zoom {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.map_controls :global .map_controls_wrapper .reset-zoom .zoom_btns {
  display: flex;
}
.map_controls :global .map_controls_wrapper .reset-zoom .zoom_btns .control_btn {
  font-size: 17px;
  padding: 0;
  width: 36px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;
}
.map_controls :global .map_controls_wrapper .reset-zoom .zoom_btns .control_btn.zoom-out {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.map_controls :global .map_controls_wrapper .reset-zoom .zoom_btns .control_btn.zoom-in {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

#threejs-map .amenities {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  border-bottom: none;
  text-align: center;
  z-index: 1;
  cursor: default;
  display: none;
}
#threejs-map .amenities .amenity {
  cursor: pointer;
  background: linear-gradient(135deg, rgb(92, 145, 197) 0%, rgb(31, 102, 174) 100%);
  transition: opacity 300ms;
  position: relative;
  overflow: hidden;
}
#threejs-map .amenities .amenity:after {
  content: "";
  background-color: rgb(92, 145, 197);
  background: linear-gradient(135deg, rgb(92, 145, 197) 0%, rgb(31, 102, 174) 100%);
  position: absolute;
  left: 40%;
  top: 30%;
  z-index: 1;
  height: 160%;
  width: 160%;
  border-radius: 9999px;
}
#threejs-map .amenities .amenity + .amenity {
  border-top: 1px solid white;
}
#threejs-map .amenities .amenity:first-child {
  border-top-right-radius: 6px;
}
#threejs-map .amenities .amenity:last-child {
  border-bottom-right-radius: 6px;
}
#threejs-map .amenities .amenity .content {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 6px 4px;
}
#threejs-map .amenities .amenity .content .icon {
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
}
#threejs-map .amenities .amenity .content .icon svg {
  width: 80%;
  height: 80%;
}
#threejs-map .amenities .amenity .content .name {
  color: white;
  font-size: 8px;
}