.mapBoxSidebar {
  flex: 0 0 0px;
  overflow: hidden;
  transition: all 300ms;
}
.mapBoxSidebar.active {
  flex: 0 0 388px;
}
.mapBoxSidebar .sidebarWrapper {
  padding-right: 15px;
  height: 100%;
  width: 388px;
}
.mapBoxSidebar .sidebarWrapper .sidebarBox {
  background-color: #FFF;
  padding: 15px;
  border: 1px solid #e6edef;
  height: 100%;
  overflow-y: auto;
}

.mapBox {
  position: relative;
  width: 100%;
}
.mapBox .storeNamePlaceholder {
  display: none;
  position: absolute;
  padding: 10px;
  background: #ffffff;
  font-weight: 600;
  color: #000000;
  font-size: 16px;
  opacity: 0.8;
}

.stats {
  position: absolute !important;
}