.retailerInfoModal {
  max-width: 600px !important;
  min-width: 360px;
}
.retailerInfoModal :global .modal-content {
  overflow: visible !important;
}
.retailerInfoModal .retailerInfoCloseBtn {
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(50%, -50%);
  cursor: pointer;
  width: 24px;
  height: 24px;
  z-index: 1;
}
.retailerInfoModal .retailerInfoCloseBtn .closeBtn {
  background: black;
  border-radius: 50%;
  cursor: pointer;
}
.retailerInfoModal .modalBody {
  display: flex;
  padding: 0;
  flex-direction: column;
  min-height: 0;
}
.retailerInfoModal .modalBody .contentWrapper {
  padding: 1rem;
  flex-grow: 1;
  overflow-y: auto;
  overflow-x: hidden;
}
.retailerInfoModal .modalBody .contentWrapper .content .retailerLogo {
  text-align: center;
  margin-bottom: 20px;
}
.retailerInfoModal .modalBody .contentWrapper .content .retailerLogo .img {
  max-width: 160px;
  max-height: 160px;
}
.retailerInfoModal .modalBody .contentWrapper .content .retailerInfoContent {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.retailerInfoModal .modalBody .contentWrapper .content .retailerInfoContent :global a {
  color: #0763b1;
}
.retailerInfoModal .modalBody .getDirectionsFrom {
  padding: 1rem;
  box-shadow: 0px 0px 10px 4px rgba(0, 0, 0, 0.1);
}
.retailerInfoModal .modalBody .getDirectionsFrom .retailerDropdown {
  box-shadow: none !important;
  outline: none !important;
  border-color: var(--bs-border-color) !important;
}
.retailerInfoModal .modalBody .getDirectionsFrom .btn {
  background-color: #0763b1 !important;
}