:root {
  --accentBlueColor: #0763b1;
  --accentGreenColor: #81be41;
}

.checkbox-field {
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  grid-gap: 6px;
  margin: 0;
}
.checkbox-field input {
  display: none;
}
.checkbox-field input + .tickbox {
  width: 17px;
  height: 17px;
  position: relative;
  border: 1px solid #ccc;
  border-radius: 2px;
  display: block;
}
.checkbox-field input:checked + .tickbox {
  border-color: var(--accentBlueColor);
  background-color: var(--accentBlueColor);
}
.checkbox-field input:checked + .tickbox:after {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  width: 11px;
  height: 7px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid white;
  border-top-color: transparent;
  border-right-color: transparent;
  transform: rotate(-45deg);
}
.checkbox-field input.partial-checked + .tickbox {
  background-color: var(--accentBlueColor);
  border-color: var(--accentBlueColor);
}
.checkbox-field input.partial-checked + .tickbox:after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  width: 8px;
  height: 2px;
}
.checkbox-field input:disabled + .tickbox {
  background: #EEE;
  opacity: 0.5;
}

.radio-field {
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  grid-gap: 8px;
}
.radio-field input {
  display: none;
}
.radio-field input + .tickbox {
  width: 18px;
  height: 18px;
  position: relative;
  border: 2px solid var(--secondaryColor3);
  border-radius: 50%;
}
.radio-field input:checked + .tickbox:after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 10px;
  height: 10px;
  background-color: var(--accentBlueColor);
  transform: translate(-50%, -50%);
  border-radius: 50%;
}
.radio-field input[disabled] + .tickbox {
  border-color: var(--textColor5);
}
.radio-field input[disabled]:checked + .tickbox:after {
  background-color: var(--textColor5);
}