.escalator_elems {
  display: none;
}
.escalator_elems .element {
  position: absolute;
  top: -21px;
  left: -21px;
  width: 42px;
  height: 42px;
  border: 4px solid #ccc;
  background-color: #4ea5ff;
  box-sizing: border-box;
  border-radius: 50%;
  cursor: pointer;
  z-index: 1;
}
.escalator_elems .element:hover {
  z-index: 2;
}
.escalator_elems .element .svgIcon {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
}
.escalator_elems .element .svgIcon svg {
  width: 75%;
  height: 75%;
}
.escalator_elems .element .label {
  display: flex;
  background-color: #fff;
  white-space: nowrap;
  position: absolute;
  padding: 4px 12px;
  border-radius: 200px;
  align-items: center;
  color: #444;
  font-size: 14px;
  font-weight: 600;
  left: 50%;
  top: 0;
  transform: translate(-50%, calc(-100% - 12px));
  box-shadow: 2px 2px 6px 0px rgba(0, 0, 0, 0.3);
}
.escalator_elems .element .label:before {
  content: "";
  position: absolute;
  top: calc(100% - 8px);
  left: 50%;
  transform: translateX(-50%) rotate(45deg);
  border: 6px solid #fff;
  border-color: transparent #fff #fff transparent;
}